body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.SearchSys{
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-end;
    min-height: 100%;
}

#searchInput{
    margin-left: 10px;
    height: 70%;
    padding: 0 10px;
    border: none;
    background-color: rgb(194, 194, 194);
}

#submitButton{
    height: 70%;
    margin: 0 10px;
    border: none;
    background-color: (194, 194, 194);
    color:black;
    font-size: 1em;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
}




header{
    min-height: 70px;
    display: flex;
    flex-flow: row wrap;
    justify-content: space-between;
    align-items: center;
    background-color: rgb(75, 30, 34);
    width: 100%;
}

header .logo{
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    height: 80%;
}

header .logo img{
    height: 60%;
    max-height: 60px;
    padding: 10px;
    margin: 10px;
    background-color: rgb(194, 194, 194);
    border-radius: 100px;
}

header .logo h1{
    color: rgb(194, 194, 194);
}

header .searchBar{
    height: 100%;
    min-height: 50px;
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-end;
}

header .nav-menu{
    list-style-type: none;
    display: flex;
    flex-flow: row nowrap;
}

header .nav-menu li a{
    color: rgb(194, 194, 194);
    margin: 10px;
}
.Home{
    margin-top: 10px;
}

.Home h2{
    font-size: 2em;
    color: rgb(133, 65, 71);
    text-transform: uppercase;
}

.Home section{
    width: 98vw;
}

::-webkit-scrollbar{
    width: 2px;
    height: 1px;
    background-color: black; 
}

::-webkit-scrollbar-thumb{
    border: 1px rgb(29, 29, 29) solid;
    border-radius: 10px;
}

.Home .list{
    display: flex;
    flex-flow: row nowrap;
    justify-content: flex-start;
    overflow-x: scroll;
}

.Home .list .item {
    margin: 0 10px;
}

.Home .list .item h3{
    text-transform: uppercase;
}

.Home .poster {
    width: 200px;
    height: 300px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }

.display-block {
    display: block;
}
  
.display-none {
    display: none;
}

.modal-main {
    position:fixed;
    background: rgba(0, 0, 0, 0);
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    }

.video{
    background-color: rgba(0, 0, 0, 0);
}

.modal-main button{
    background-color: rgba(0, 0, 0, 0);
    border: none;
    color: white;
    cursor: pointer;
    font-size: 0.8em;
    font-weight: bold;
}
.Card{
    display: flex;
    flex-flow: row nowrap;
    width: 88%;
    margin: 3%;
    padding: 3%;
    height: 310px;
    border: 1px solid grey;
    border-radius: 10px;
    box-shadow: 0 -1px 7px rgb(80, 80, 80) inset;
}

.Card .content{

    display: flex;
    flex-flow: column nowrap;
    justify-content: flex-start;
    align-items: center;
}

.Card .content p{
    text-align: justify;
    padding: 10px;
    overflow-y: scroll;
    font-size: 0.8em;
}

.Card img{
    border-radius: 10px;
    max-width: 200px;
}

.Card h2{
    padding: 10px;
}

.Card .trailerLink{
    border: none;
    padding: 10px;
    cursor: pointer;
} 

.mod{
    width: 50%;
}

 
.Results{
    margin-top: 100px;
}
.Contact{
    display: flex;
    flex-flow: row;
    justify-content: center;
}

.Contact form{
    width: 100%;
    max-width: 400px;
    margin: 20px;
    padding: 20px;
    border: white 1px solid;
    background-color: rgb(194, 194, 194);
    border-radius: 5px;
    box-shadow: 3px 3px 20px rgb(51, 51, 51);
}

.Contact fieldset{
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    align-items: center;
    text-align: center;
    border: none;
}

.Contact label {
    color: black;
}

.Contact input[type='text'], .Contact textarea{
    width: 100%;
    margin: 10px 0;
    background-color: black;
    border: none;
    padding: 6px;
    color: rgb(194, 194, 194);
    font-family: Arial, Helvetica, sans-serif;
}

.Contact input[type='submit']{
    height: 50px;
    margin: 0 10px;
    padding: 10px;
    border: none;
    background-color: black;
    color:rgb(194, 194, 194);
    font-size: 1em;
    font-weight: bold;
    text-transform: uppercase;
    cursor: pointer;
}

.Contact textarea{
    height: 100px;
}
.Browse .inputs {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  margin: 20px 0;
}

.Browse fieldset {
  display: flex;
  flex-flow: column nowrap;
  border : none;
  height: 100%;
}

.Browse label {
  padding: 10px;
}

.Browse select {
  width: 150px;
  height: 100px;
  background-color: #000;
  color: rgb(194, 194, 194);
  border-radius: 3px;
  border: 1px dotted rgb(194, 194, 194);
  padding: 3px;
}

.Browse input[type='range']{
  width: 100%;
  color: rgb(194, 194, 194);
  border-radius: 20px;
}

.Browse .releaseDateInputs{
  display: flex;
  flex-flow: row nowrap;
}

.Browse input[type='date']{
  border: none;
  background-color: black;
  color: rgb(194, 194, 194);
}

input[type="date"]::-webkit-inner-spin-button{
  display: none;
}

.Browse input[type='submit']{
  height: 50px;
  margin: 0 10px;
  padding: 10px;
  border: none;
  background-color: rgb(194, 194, 194);
  color: black;
  font-size: 1em;
  font-weight: bold;
  text-transform: uppercase;
  cursor: pointer;
}
body {
  text-align: center;
  background-color: black;
  color: rgb(194, 194, 194);
}

a {
  text-decoration: none;
  color: black;
}




