.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }

.display-block {
    display: block;
}
  
.display-none {
    display: none;
}

.modal-main {
    position:fixed;
    background: rgba(0, 0, 0, 0);
    width: 80%;
    height: auto;
    top:50%;
    left:50%;
    transform: translate(-50%,-50%);
    }

.video{
    background-color: rgba(0, 0, 0, 0);
}

.modal-main button{
    background-color: rgba(0, 0, 0, 0);
    border: none;
    color: white;
    cursor: pointer;
    font-size: 0.8em;
    font-weight: bold;
}